import React, { useState } from 'react';
import Navbar from './components/Navbar';
import SectionDropdown from './components/SectionDropdown';
import Section from './components/Section';
import './index.css';

import sections from 'https://mohan-chinnappan-n5.github.io/apps/3/sections.js'

const App = () => {
  // Default to show all sections
  const [selectedSections, setSelectedSections] = useState([{ value: 'all', label: 'All' }]);

  const handleSectionChange = (selectedOptions) => {
    setSelectedSections(selectedOptions || []);
  };
  

  const allSections = Object.keys(sections).map(key => ({
    value: key,
    ...sections[key]
  }));

  const sectionsToShow = selectedSections.some(option => option.value === 'all')
    ? allSections
    : selectedSections.map(option => sections[option.value] || { title: 'Section', links: [] });

  const total = Object.values(sections).reduce((acc, section) => acc + section.links.length, 0);

  return (
    <div className='bg-white-100'>
      <Navbar />
      <SectionDropdown
        selectedOptions={selectedSections}
        onChange={handleSectionChange}
      />

      <div className="absolute top-4 right-4 bg-red-500 text-white text-xs font-medium px-3 py-1 rounded-full shadow-lg">
        Total: {total + 12}
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4 p-4">
        {sectionsToShow.map(section => (
          <Section
            key={section.value}
            title={section.title || 'Section'}
            links={section.links || []}
          />
        ))}
      </div>
    </div>
  );
};

export default App;
